

























import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import dayjs from 'dayjs'

@Component
export default class CalendarField extends Vue {
    /**
     * @category Refs
     */

    $refs!: Vue['$refs'] & {
        input: HTMLInputElement
    }

    /**
     * @category Props
     */

    /** form  id */
    @Prop({ type: String, required: true })
    readonly id!: string

    /** label   */
    @Prop({ type: String, default: '', required: true })
    readonly label!: string

    /** label  */
    @Prop(Boolean)
    readonly hiddenLabel!: boolean

    /**   */
    @Prop(Date)
    readonly defaultValue!: Date

    /**  */
    @Prop({ type: Boolean, default: false })
    readonly readonly!: boolean

    /**
     * @category Data(State)
     */

    /** focus  */
    private focusedClass: boolean = false

    /**   */
    private value: Date | null = this.defaultValue || new Date()

    /** datepicker   */
    private datepickerVisible: boolean = false

    /** datepicker   transition */
    private transitionProps: { [key: string]: TransitionProps } = {
        datepicker: {
            mode: 'out-in',
            'enter-active-class': 'animate__animated animate__bounceInUp',
            'leave-active-class': 'animate__animated animate__bounceOutDown',
        },
    }

    /**
     * @category Computed
     */

    /**   value */
    get displayValue(): string {
        return dayjs(this.value || undefined).format('YYYY.MM.DD')
    }

    @Watch('value')
    changeValue(newValue: Date, oldValue: Date) {
        /**
         *     
         * @event change
         */
        this.$emit('change', {
            value: newValue,
            fieldName: this._.camelCase(this.id),
        })
    }

    /**
     * @category Methods
     */

    onKeydown(event: KeyboardEvent) {
        event.preventDefault()
    }

    onDayClick(day: { date: Date }) {
        this.hideDatepicker()
    }

    onClickIcon() {
        this.$refs.input.focus()
    }

    onFocus(event: FocusEvent) {
        /**
         * focus 
         * @event focus
         */
        this.$emit('focus', event)

        this.focusedClass = true
        this.showDatepicker()
    }

    onBlur(event: FocusEvent) {
        this.focusedClass = false
    }

    hideDatepicker() {
        this.datepickerVisible = false
    }

    showDatepicker() {
        this.datepickerVisible = true
    }

    deleteValue() {
        this.value = null
    }
}
