











































import { Component, Prop, Vue } from 'vue-property-decorator'

// TODO:        
type DesignType = 'select' | 'banner' | 'description'

@Component
export default class BottomSheet extends Vue {
    /**
     * @category Props
     */

    /**  */
    @Prop(String)
    readonly title!: string

    /**  */
    @Prop(String)
    readonly description!: string

    /**  */
    @Prop(String)
    readonly subText!: string

    /**   */
    @Prop({ type: Boolean, required: true })
    readonly show!: boolean

    /**   */
    @Prop({ type: String, default: '' })
    readonly type!: DesignType

    /**    */
    @Prop({ type: Array, default: () => ({}) })
    readonly list!: BottomSheetOptionItem[]

    /**
     * @category Data(State)
     */
    private transitionBeforeClass: string = ''

    /**
     * @category Computed
     */

    /**
     * @category Methods
     */
    onClose() {
        /**
         * 
         * @event close
         */
        this.$emit('close')
    }

    onClickOption(index: number) {
        /**
         * list option   
         * @event select-option
         */
        this.$emit('select-option', this.list[index].value)
        this.onClose()
    }
}
