









import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class FormTextBox extends Vue {
    /**
     * @category Props
     */

    /** label */
    @Prop({ type: String })
    readonly label!: string

    /** label  */
    @Prop(Boolean)
    readonly hiddenLabel!: boolean

    /** type :  row /  col */
    @Prop({ type: String, default: 'row' })
    readonly type!: string
}
