











import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class Dimmed extends Vue {
    /**
     * @category Props
     */

    /**   */
    @Prop({ type: Boolean, default: false, required: true })
    readonly show!: boolean

    /**
     * @category Methods
     */

    onClose() {
        /**
         * click  
         * @event click
         */
        this.$emit('click')
    }
}
